@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{margin:0px;padding:0px;box-sizing:content-box}body{overflow-x:hidden}.form-control{width:95%}:root{--color-black-main:hsl(218, 23%, 33%);--color-black-main-hover:hsl(218, 23%, 27%);--color-black-main-h:218;--color-black-main-s:23%;--color-black-main-l:33%;--color-orange:hsl(20, 100%, 61%);--color-orange-hover:hsl(20, 100%, 55%);--color-orange-h:20;--color-orange-s:100%;--color-orange-l:61%;--color-green:hsl(118, 41%, 62%);--color-green-hover:hsl(118, 41%, 56%);--color-green-h:118;--color-green-s:41%;--color-green-l:62%}.header{position:relative;font-family:"Pacifico",cursive;font-size:2rem;padding:0px 40px;color:hsl(218, 23%, 33%);color:var(--color-black-main)}@media(max-width: 600px){.header{padding:0px 10px}}.header .header__btn{font-family:"Roboto",sans-serif;float:right;font-size:1.2rem;vertical-align:middle;padding:2px 10px;margin-top:10px;color:#fff;background-color:hsl(20, 100%, 61%);background-color:var(--color-orange);border-radius:10px}.content{position:relative;width:calc(100% - 80px);margin-left:40px}@media(max-width: 600px){.content{width:calc(100% - 20px);margin-left:10px}}.banner{margin-top:10px;position:relative;min-height:100px;border-radius:20px;background-color:hsl(118, 41%, 62%);background-color:var(--color-green);font-family:"Roboto",sans-serif}.banner::after{content:"";position:absolute;width:2px;height:90%;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);background-color:#fff;border-radius:7px}@media(max-width: 600px){.banner::after{background-color:transparent}}.banner>.banner__heading{color:#fff;font-size:4rem;font-weight:500;padding-top:30px;padding-left:29px;padding-bottom:5px}@media(max-width: 600px){.banner>.banner__heading{font-size:2rem}}.banner>.banner__sub-heading{color:#fff;font-size:1rem;padding-left:29px;padding-bottom:40px}.banner>.banner__virus{display:flex;position:relative;padding-left:29px}@media(min-width: 600px){.banner>.banner__virus{position:absolute;left:75%;top:50%;border-radius:9px;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}}.banner>.banner__virus .virus-name{font-size:1.5rem;color:#fff;margin-right:5px}.banner>.banner__virus .edit-user{position:relative;cursor:pointer;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;margin-bottom:25px}.banner>.banner__virus .edit-user img{position:relative;top:3px;width:35px;height:35px}.nav-tab{position:relative;width:100%;display:flex;margin-top:30px;font-family:"Roboto",sans-serif}.nav-tab .nav-item{position:relative;flex-grow:1;text-align:center;cursor:pointer}.nav-tab .nav-item:first-child::after{content:"";position:absolute;width:5px;height:calc(100% + 15px);right:-5.5px;top:-7.5px;background-color:hsl(118, 41%, 62%);background-color:var(--color-green);border-radius:7px}.nav-tab .nav-item.active::before{content:"";position:absolute;width:120px;height:calc(100% + 10px);left:50%;top:50%;border-radius:9px;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);background-color:hsla(20, 100%, 61%, 0.2);background-color:hsla(var(--color-orange-h), var(--color-orange-s), var(--color-orange-l), 0.2);z-index:-1}.nav-tab .nav-item:hover::before{content:"";position:absolute;width:120px;height:calc(100% + 10px);left:50%;top:50%;border-radius:9px;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);background-color:hsla(20, 100%, 61%, 0.2);background-color:hsla(var(--color-orange-h), var(--color-orange-s), var(--color-orange-l), 0.2);z-index:-1}.nav-content{margin-top:30px}.add-user{position:absolute;cursor:pointer;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;margin-bottom:25px}.add-user img{position:relative;top:-1.5px;width:20px;height:20px}.user-list{list-style:none;padding-left:0px}.user-list li{border:1px solid hsl(118, 41%, 62%);border:1px solid var(--color-green);background-color:hsla(118, 41%, 62%, 0.01);background-color:hsla(var(--color-green-h), var(--color-green-s), var(--color-green-l), 0.01);padding:4px 8px;border-radius:5px;margin-bottom:5px}.user-list li.green{border:1px solid hsl(118, 41%, 62%);border:1px solid var(--color-green)}.user-list li.red{border:1px solid red}
